import React, { Component } from 'react';
import Task_form from './components/Task_form/Task_form';
import Task_list from './components/Task_list/Task_list'
import Search from './components/Search/Search'

class App extends Component {
  constructor(props) {
    super(props)
    this.state = {
      _tasks_: [],
      _status_add_form_: false,
      _edit_task_: null,
      _key_word_: ''
    }
    this._func_random_id = this._func_random_id.bind(this)
    this._func_load_data_ = this._func_load_data_.bind(this)
    this._func_display_form_ = this._func_display_form_.bind(this)
    this._func_close_form_ = this._func_close_form_.bind(this)
    this._func_add_new_data_ = this._func_add_new_data_.bind(this)
    this._func_update_status_ = this._func_update_status_.bind(this)
    this._func_delete_ = this._func_delete_.bind(this)

  }
  componentWillMount() {
    this._func_load_data_()
  }
  _func_random_id() {
    return Math.floor((1 + Math.random()) * 0x10000).toString(16).substring(1)
  }
  _func_generate_id_() {
    return this._func_random_id() + '-' + this._func_random_id() + '-' + this._func_random_id() + '-' + this._func_random_id() + '-' + this._func_random_id() + '-' + this._func_random_id() + '-' + this._func_random_id() + '-' + this._func_random_id()
  }
  _func_load_data_() {
    var _task_list_ = [
      {
        _id_: this._func_generate_id_(),
        _name_: 'Tap gym',
        _status_: true

      },
      {
        _id_: this._func_generate_id_(),
        _name_: 'Tap vo',
        _status_: false

      },
      {
        _id_: this._func_generate_id_(),
        _name_: 'Tap nhay',
        _status_: false

      }
    ]

    this.setState({
      _tasks_: _task_list_
    })
  }

  _func_margin_() {
    return {
      marginRight: 20
    }
  }
  _func_display_form_() {
    this.setState({
      _status_add_form_: !this.state._status_add_form_
    })
  }

  _func_close_form_() {
    this.setState({
      _status_add_form_: false
    })
  }
  _func_show_form_ = () => {
    this.setState({
      _status_add_form_: true
    })
  }
  _func_add_new_data_(_new_data_) {
    var _tasks_ = this.state._tasks_
    var _task_ = null
    var _index_ = null
    if (_new_data_._id_ === "") {
      _task_ = {
        _id_: this._func_generate_id_(),
        _name_: _new_data_._name_,
        _status_: _new_data_._status_
      }
      _tasks_.push(_task_)
    }
    else {
      _index_ = this._func_find_index_(_new_data_._id_)
      _tasks_[_index_] = _new_data_
    }
    this.setState({
      _tasks_: _tasks_,
      _edit_task_: null
    })
  }

  _func_find_index_(_id_) {
    var _tasks_ = this.state._tasks_
    var result

    _tasks_.forEach((_task_, _index_) => {
      if (_task_._id_ === _id_) {
        result = _index_
      }
    })
    return result
  }

  _func_update_status_(_id_) {
    var _tasks_ = this.state._tasks_
    var _index_ = this._func_find_index_(_id_)
    if (_index_ !== -1) {
      _tasks_[_index_]._status_ = !_tasks_[_index_]._status_
      this.setState({
        _tasks_: _tasks_
      })
    }
  }
  _func_delete_(_id_) {
    var _tasks_ = this.state._tasks_
    var _index_ = this._func_find_index_(_id_)
    if (_index_ !== -1) {
      _tasks_.splice(_index_, 1)
      this.setState({
        _tasks_: _tasks_
      })
    }
    this._func_close_form_()
  }
  _funct_update_ = (id) => {
    var _tasks_ = this.state._tasks_
    var _index_ = this._func_find_index_(id)
    var _edit_task_ = _tasks_[_index_]
    this.setState({
      _edit_task_: _edit_task_
    })
    this._func_show_form_()
  }
  _func_search_ = (keyword) => {
    console.log(keyword)
    this.setState({
      _key_word_: keyword
    })
    console.log(this.state._key_word_)
  }
  render() {
    var keyword = this.state._key_word_
    var _task_ = this.state._tasks_
    var _status_add_form_ = this.state._status_add_form_
    var _display_form_ = _status_add_form_ ? <Task_form _edit_task_prop_={this.state._edit_task_} _add_new_data_={this._func_add_new_data_} _close_form_prop_={this._func_close_form_} /> : " "
    if (keyword) {
      _task_ = _task_.filter((task) => {
        return task._name_.toLowerCase().indexOf(keyword) !== -1
      })
    }
    return (
      <div>
        <Search _key_wrod_prop_={this._func_search_} />
        <Task_list _Id_prop_={this._funct_update_} _task_list_={_task_} _update_status_prop_={this._func_update_status_} _delete_prop_={this._func_delete_} />
        <button type="button" onClick={this._func_display_form_}>Add</button>
        {_display_form_}
      </div>
    );
  }
}

export default App;
