import React, { Component } from 'react';

class Search extends Component {
  constructor(props) {
    super(props)
    this.state = {
      _key_word_: ''
    }
  }
  _func_change_ = (event) => {
    var target = event.target
    var name = target.name
    var value = target.value
    this.setState({
      [name]: value
    })
  }
  _func_serach_ = () => {
    this.props._key_wrod_prop_(this.state._key_word_)
  }
  render() {
    return (
      <div>
        <input type="text" name="_key_word_" value={this.state._key_word_} onChange={this._func_change_} />
        <button type="button" onClick={this._func_serach_}>Search</button>
      </div>
    );
  }
}

export default Search;
