import React, { Component } from 'react';

class Task_item extends Component {
  constructor(props) {
    super(props)
    this._func_change_status_ = this._func_change_status_.bind(this)
    this._func_delete_ = this._func_delete_.bind(this)
  }
  _func_change_status_() {
    this.props._update_status_prop_(this.props._task_._id_)
  }
  _func_delete_() {
    this.props._delete_prop_(this.props._task_._id_)
  }
  _func_update_ = () => {
    this.props._getId_prop_(this.props._task_._id_)
  }
  render() {
    var _task_ = this.props._task_
    var _no_ = this.props._no_ + 1
    return (
      <tr>
        <td>{_no_}</td>
        <td>{_task_._id_}</td>
        <td>{_task_._name_}</td>
        <td><span onClick={this._func_change_status_}>{_task_._status_ ? "Hoat dong" : "Khong hoat dong"}</span></td>
        <td>
          <button onClick={this._func_update_} type="button">o</button>
          <button onClick={this._func_delete_} type="button">x</button>
        </td>
      </tr>
    );
  }
}

export default Task_item;
