import React, { Component } from 'react';
import Task_item from '../Task_item/Task_item';

class Task_list extends Component {
  _func_update_ = (id) => {
    this.props._Id_prop_(id)
  }
  render() {
    var _tasks_ = this.props._task_list_
    var _task_display_ = _tasks_.map((_task_, _index_) => {
      return (
        <Task_item key={_index_} _getId_prop_={this._func_update_} _no_={_index_} _task_={_task_} _update_status_prop_={this.props._update_status_prop_} _delete_prop_={this.props._delete_prop_} />
      )
    })
    return (
      <div>
        <table border="1" cellPadding="10" cellSpacing="0">
          <thead>
            <tr>
              <th>No.</th>
              <th>Id</th>
              <th>Name</th>
              <th>Status</th>
              <th>#</th>
            </tr>
          </thead>
          <tbody>
            {_task_display_}
          </tbody>
        </table>
      </div>
    );
  }
}

export default Task_list;
