import React, { Component } from 'react';

class Task_form extends Component {
  constructor(props) {
    super(props)
    this.state = {
      _id_: '',
      _name_: '',
      _status_: false
    }

    this._func_close_form_ = this._func_close_form_.bind(this)
    this._func_submit_ = this._func_submit_.bind(this)
    this._func_change_value_ = this._func_change_value_.bind(this)
    this._func_clear_ = this._func_clear_.bind(this)
  }
  componentWillMount() {
    if (this.props._edit_task_prop_) {
      this.setState({
        _id_: this.props._edit_task_prop_._id_,
        _name_: this.props._edit_task_prop_._name_,
        _status_: this.props._edit_task_prop_._status_
      })
    }
  }
  componentWillReceiveProps(nextprop) {
    if (nextprop && nextprop._edit_task_prop_) {
      this.setState({
        _id_: nextprop._edit_task_prop_._id_,
        _name_: nextprop._edit_task_prop_._name_,
        _status_: nextprop._edit_task_prop_._status_
      })
    }
  }
  _func_close_form_() {
    this.props._close_form_prop_()
  }
  _func_change_value_(_event_) {
    var _target_ = _event_.target
    var _name_ = _target_.name
    var _value_ = _target_.value
    if (_name_ === "_status_") {
      _value_ = _target_.value === "true" ? true : false
    }
    this.setState({
      [_name_]: _value_
    })
  }
  _func_submit_(_event_) {
    _event_.preventDefault()
    this.props._add_new_data_(this.state)
    this._func_clear_()
    this._func_close_form_()
  }

  _func_clear_() {
    this.setState({
      _name_: '',
      _status_: false
    })
  }



  render() {
    var _edit_task_ = this.props._edit_task_prop_
    return (
      <div>
        <button type="button" onClick={this._func_close_form_}>x</button>
        <form onSubmit={this._func_submit_}>
          <div>
            <label>Name:</label>
            <input type="text" name="_name_" value={this.state._name_} onChange={this._func_change_value_} />
          </div>
          <div>
            <label>Status:</label>
            <select name="_status_" value={this.state._status_} onChange={this._func_change_value_}>
              <option value={true}>Hoat dong</option>
              <option value={false}>Khong hoat dong</option>
            </select>
          </div>
          <button type="submit">Save</button>
          <button type="button" onClick={this._func_clear_}>Cancel</button>
        </form>
      </div>
    );
  }
}

export default Task_form;
